import uniRequest from 'uni-request';

export default {
	props: {
		itemList: {
			type: Array,
			default: function(e){
				return {}
			}
		},
		
	},
	data() {
		return {};
	},
	onLoad() {
		let _this=this;
		uniRequest.post('/tourguide/gethometourguidelist', { 
				}).then(function (res) {
					console.log(res.data.data);
					_this.artList=res.data.data
				}).catch(function (error) {
					console.log(error);
				});
	},
	methods: {
		artList(id){
			var id  = id
			uni.navigateTo({
				url: `tourGuideParticulars?id=${id}`
			});
		},
	}
};

